import { Form } from "antd";
import clsx from "clsx";
import React, { forwardRef } from "react";
import CCInput from "src/components/CCInput";
import { SELECT } from "src/contants/Common";
import styles from "../DaiDienPhapLuat/styles.module.scss";

const BASE_FORM = ["change_info", "location"];
const DiaChiTruSoChinh = forwardRef((props, ref) => {
  return (
    <Form.Item
      label={<h4>ng k thay i a ch tr s chnh</h4>}
      // bordered={false}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      {/* <CCInput label="Tn doanh nghip" name={[...BASE_FORM, "company_name"]} />

      <CCInput label="M s doanh nghip/ m s thu" name={[...BASE_FORM, "mst"]} /> */}

      <Form.Item label={<h4>a ch tr s hin ti</h4>}>
        <CCInput name={[...BASE_FORM, "old", "address"]} label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn" />
        <CCInput name={[...BASE_FORM, "old", "town"]} label="X/Phng/Th Trn" />
        <CCInput name={[...BASE_FORM, "old", "district"]} label="Qun/Huyn/Th X/Thnh ph thuc tnh" />
        <CCInput name={[...BASE_FORM, "old", "city"]} label="Tnh/Thnh ph" />
      </Form.Item>

      <Form.Item name={[...BASE_FORM, "new_location"]} label={<h4>a ch tr s sau khi thay i</h4>}>
        <CCInput
          name={[...BASE_FORM, "new_location", "address"]}
          label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
        />
        <CCInput name={[...BASE_FORM, "new_location", "town"]} label="X/Phng/Th Trn" />
        <CCInput name={[...BASE_FORM, "new_location", "district"]} label="Qun/Huyn/Th X/Thnh ph thuc tnh" />
        <CCInput name={[...BASE_FORM, "new_location", "city"]} label="Tnh/Thnh ph" />
      </Form.Item>

      <CCInput name={[...BASE_FORM, "phone"]} label="in thoi" />

      <CCInput
        type="select"
        name={[...BASE_FORM, "inside"]}
        label="Doanh nghip nm trong"
        options={SELECT.BUSINESS_LOCATION}
      />

      <CCInput name={[...BASE_FORM, "legal_person"]} label="Tn ngi i din php lut" />
      {/* </Form> */}
    </Form.Item>
  );
});

export default DiaChiTruSoChinh;
