import { Form } from "antd";
import clsx from "clsx";
import React, { forwardRef } from "react";
import CCInput from "src/components/CCInput";
import styles from "../CreateCompany.module.scss";

const DiaChiTruSoChinh = forwardRef((props, ref) => {
  const { BASE_FORM, current } = props;

  return (
    <Form.Item
      label={<h2>a ch t tr s</h2>}
      className={clsx([
        styles.hide,
        {
          [styles.visible]: current === 6,
        },
      ])}
    >
      <Form.Item label="a ch tr s chnh">
        <CCInput
          name={[...BASE_FORM, "core", "address", "street"]}
          label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
        />
        <CCInput name={[...BASE_FORM, "core", "address", "town"]} label="X/Phng/Th trn" />
        <CCInput name={[...BASE_FORM, "core", "address", "district"]} label="Qun/Huyn/Th x/Thnh ph thuc tnh" />
        <CCInput name={[...BASE_FORM, "core", "address", "city"]} label="Tnh/Thnh ph" />
      </Form.Item>

      <CCInput name={[...BASE_FORM, "core", "address_opt_1"]} label="a ch chi nhnh (nu c)" />

      <CCInput name={[...BASE_FORM, "core", "address_opt_2"]} label="a ch vn phng i din (nu c)" />
    </Form.Item>
  );
});

export default DiaChiTruSoChinh;
