import { Col, Form, Row } from "antd";
import clsx from "clsx";
import React, { forwardRef, useEffect } from "react";
import CCInput from "src/components/CCInput";
import styles from "../DaiDienPhapLuat/styles.module.scss";
const BASE_FORM = ["change_info", "up_authorized_capital"];
const TangVonDieuLe = forwardRef((props, ref) => {
  useEffect(() => {
    if (ref) {
      ref.current.setFieldsValue({
        change_info: {
          up_authorized_capital: {
            type: "Tng vn gp",
          },
        },
      });
    }
  }, [ref]);

  return (
    <Form.Item
      label={<h4>ng k thay i vn iu l</h4>}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      <Row gutter={[16, 0]}>
        {/* <Col span={24}>
         <CCInput label="Tn doanh nghip" name={[...BASE_FORM, "company_name"]} />
        </Col>
        <Col span={24}>
          <CCInput label="M s doanh nghip/ m s thu" name={[...BASE_FORM, "mst"]} />
        </Col> */}
        <Col span={12}>
          <CCInput label="Vn iu l  ng k (bng s)" name={[...BASE_FORM, "base_val", "num"]} />
        </Col>
        <Col span={12}>
          <CCInput label="Vn iu l  ng k (bng ch)" name={[...BASE_FORM, "base_val", "char"]} />
        </Col>
        <Col span={12}>
          <CCInput label="Vn iu l sau khi tng (bng s)" name={[...BASE_FORM, "new_base_val", "num"]} />
        </Col>
        <Col span={12}>
          <CCInput label="Vn iu l sau khi tng (bng ch)" name={[...BASE_FORM, "new_base_val", "char"]} />
        </Col>
        <Col span={24}>
          <CCInput label="Hnh thc tng vn" name={[...BASE_FORM, "type"]} />
        </Col>
      </Row>
    </Form.Item>
  );
});

export default TangVonDieuLe;
