import clsx from "clsx";
import React from "react";
import CCInput from "src/components/CCInput";
import styles from "../styles.module.scss";

const HuyBoGiaiThe = (props) => {
  return (
    <Form.Item
      label="Hy b gii th"
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      <CCInput name={["dissolution", "cancel", "company_name"]} label="Tn doanh nghip (ghi bng ch in hoa)" />
      <CCInput name={["dissolution", "cancel", "mst"]} label="M s doanh nghip/M s thu" />
    </Form.Item>
  );
};

export default HuyBoGiaiThe;
