import { Form } from "antd";
import clsx from "clsx";
import React, { forwardRef } from "react";
import CCInput from "src/components/CCInput";
import styles from "../DaiDienPhapLuat/styles.module.scss";
const TenDoanhNghiep = forwardRef((props, ref) => {
  const base_type = [
    {
      name: "ng k thay i trn c s tch doanh nghip",
      value: "ng k thay i trn c s tch doanh nghip",
    },
    {
      name: "ng k thay i trn c s sp nhp doanh nghip",
      value: "ng k thay i trn c s sp nhp doanh nghip",
    },
  ];

  return (
    <Form.Item
      label={<h4>ng k thay i tn doanh nghip</h4>}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >

      {/* <CCInput label="Tn doanh nghip" name={["change_info", "name", "company_name"]} />

      <CCInput label="M s doanh nghip/ m s thu" name={["change_info", "name", "mst"]} /> */}

      <CCInput
        type="select"
        label="Doanh nghip ng k thay i tn c s"
        name={["change_info", "name", "base_type"]}
        options={base_type}
      />

      <Form.Item label={"Thay i tn cng ty thnh"}>
        <CCInput label="Tn cng ty bng ting Vit" name={["change_info", "name", "name_vi"]} />

        <CCInput label="Tn cng ty bng ting nc ngoi" name={["change_info", "name", "name_en"]} />

        <CCInput label="Tn cng ty vit tt" name={["change_info", "name", "name_etc"]} />

        <p>Note: Validating Company name</p>

        <CCInput label="Tn ngi i din php lut" name={["change_info", "name", "legal_person"]} />
      </Form.Item>
    </Form.Item>
  );
});

export default TenDoanhNghiep;
