import { Button, Col, Form, InputNumber, Row, Space } from "antd";
import clsx from "clsx";
import React, { forwardRef, useState } from "react";
import CCInput from "src/components/CCInput";
import { SELECT } from "src/contants/Common";
import styles from "../DaiDienPhapLuat/styles.module.scss";
const BASE_FORM = ["change_info", "transfer_contract"];
const HopDongChuyenNhuong = forwardRef((props, ref) => {
  const [sohuuA, setSohuuA] = useState();
  const [sohuuB, setSohuuB] = useState();
  const [type, setType] = useState("");

  const handleFill = () => {
    if (!ref) return;
    let val = ref.current.getFieldsValue();
    let { reg_address, town, district, city } = val.change_info.transfer_contract?.B_side?.personal;
    ref.current.setFieldsValue({
      change_info: {
        transfer_contract: {
          B_side: {
            personal: {
              contact_reg_address: reg_address,
              contact_town: town,
              contact_district: district,
              contact_city: city,
            },
          },
        },
      },
    });
  };

  const renderFormOnwerA = (condition, fieldName) => {
    let xhtml = null;

    if (condition === "personal") {
      xhtml = (
        <Row>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "personal", "name"]} label="H v tn" />
          </Col>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput type="date" name={[...fieldName, "personal", "birth_day"]} label="Ngy sinh" />
          </Col>

          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput
              type="select"
              name={[...fieldName, "personal", "doc_type"]}
              label="Loi giy t php l"
              options={SELECT.DOC_TYPE}
            />
          </Col>

          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "personal", "doc_code"]} label="S giy t php l" />
          </Col>

          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput type="date" name={[...fieldName, "personal", "doc_time_provide"]} label="Ngy cp" />
          </Col>

          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "personal", "doc_place_provide"]} label="Ni cp" />
          </Col>

          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "personal", "contact_address"]} label="a ch lin lc" />
          </Col>
        </Row>
      );
    } else {
      // Ch s hu l t chc
      // Trng hp ch s hu l t chc:
      // Tn doanh nghip
      // M s doanh nghip
      // a ch tr s chnh
      // Ngi i din theo php lut ca cng ty:
      // Chc danh:
      xhtml = (
        <Row>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "company_name"]} label="Tn doanh nghip" />
          </Col>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "mst"]} label="M s doanh nghip" />
          </Col>
          <Col span={24}>
            <Form.Item label="a ch tr s chnh">
              <CCInput
                name={[...fieldName, "organization", "company_address", "street"]}
                label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
              />

              <CCInput name={[...fieldName, "organization", "company_address", "town"]} label="X/Phng/Th trn" />

              <CCInput
                name={[...fieldName, "organization", "company_address", "district"]}
                label="Qun/Huyn/Th x/Thnh ph thuc tnh"
              />

              <CCInput name={[...fieldName, "organization", "company_address", "city"]} label="Tnh/Thnh ph" />
            </Form.Item>
          </Col>

          <Col span={24}>
            <CCInput
              name={[...fieldName, "organization", "legal_representative"]}
              label="Ngi i din theo php lut ca cng ty"
            />
          </Col>
          {/** ???? */}
        </Row>
      );
    }
    return xhtml;
  };

  const renderFormOwnerB = (condition, fieldName) => {
    let xhtml = null;

    if (condition === "personal") {
      xhtml = // ch s hu l c nhn
        (
          <Row>
            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "name"]} label="H v tn" />
            </Col>
            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput
                name={[...fieldName, "personal", "gender"]}
                label="Gii tnh"
                type="select"
                options={SELECT.GENDER}
              />
            </Col>
            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "birth_day"]} label="Ngy sinh" type="date" />
            </Col>
            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "per_type"]} label="Dn tc" />
            </Col>
            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput
                type="select"
                name={[...fieldName, "personal", "doc_type"]}
                label="Loi giy t php l"
                options={SELECT.DOC_TYPE}
              />
            </Col>

            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "doc_code"]} label="S giy t php l" />
            </Col>

            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "doc_time_provide"]} label="Ngy cp" type="date" />
            </Col>

            <Col lg={12} md={12} sm={24} xs={24}>
              <CCInput name={[...fieldName, "personal", "doc_place_provide"]} label="Ni cp" />
            </Col>

            <Col lg={12} md={12} sm={24} xs={24}>
              <Form.Item label="a ch thng tr">
                <CCInput
                  name={[...fieldName, "personal", "reg_address"]}
                  label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
                />

                <CCInput name={[...fieldName, "personal", "town"]} label="X/Phng/Th Trn" />

                <CCInput name={[...fieldName, "personal", "district"]} label="Qun/Huyn/Th X/Thnh ph thuc tnh" />

                <CCInput name={[...fieldName, "personal", "city"]} label="Tnh/Thnh ph" />
              </Form.Item>
            </Col>
            <Col lg={12} md={12} sm={24} xs={24}>
              <Form.Item label="a ch lin lc">
                <Button onClick={handleFill}>T ng in</Button>
                <CCInput
                  name={[...fieldName, "personal", "contact_reg_address"]}
                  label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
                />

                <CCInput name={[...fieldName, "personal", "contact_town"]} label="X/Phng/Th Trn" />

                <CCInput
                  name={[...fieldName, "personal", "contact_district"]}
                  label="Qun/Huyn/Th X/Thnh ph thuc tnh"
                />

                <CCInput name={[...fieldName, "personal", "contact_city"]} label="Tnh/Thnh ph" />
              </Form.Item>
            </Col>
          </Row>
        );
    } else {
      // Trng hp ch s hu l t chc:
      xhtml = (
        <Row>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "company_name"]} label="Tn doanh nghip" />
          </Col>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "mst"]} label="M s doanh nghip" />
          </Col>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "time_provide"]} label="Ngy cp" />
          </Col>
          <Col lg={12} md={12} sm={24} xs={24}>
            <CCInput name={[...fieldName, "organization", "place_provide"]} label="Ni cp" />
          </Col>
          <Col span={24}>
            <Form.Item label="a ch tr s chnh">
              <CCInput
                name={[...fieldName, "organization", "company_address", "street"]}
                label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
              />

              <CCInput name={[...fieldName, "organization", "company_address", "town"]} label="X/Phng/Th trn" />

              <CCInput
                name={[...fieldName, "organization", "company_address", "district"]}
                label="Qun/Huyn/Th x/Thnh ph thuc tnh"
              />

              <CCInput name={[...fieldName, "organization", "company_address", "city"]} label="Tnh/Thnh ph" />
            </Form.Item>
          </Col>

          <Col span={24}>
            <CCInput
              type="select"
              name={[...fieldName, "organization", "company_model"]}
              label="M hnh cng ty"
              options={SELECT.COMPANY_MODEL}
            />
          </Col>
          <Col span={24}>
            <CCInput
              name={[...fieldName, "organization", "legal_representative"]}
              label="Ngi i din theo php lut ca cng ty"
            />
          </Col>
          <Col span={24}>
            <CCInput name={[...fieldName, "organization", "legal_title"]} label="Chc danh" />
          </Col>

          <Col span={24}>
            <Form.Item label="Bn A hin ang s hu phn vn gp l">
              <Form.Item name={[...fieldName, "organization", "capital_contribution", "current_value"]}>
                <InputNumber />
              </Form.Item>
              <CCInput
                type="select"
                style={{ width: "100%" }}
                name={[...fieldName, "organization", "capital_contribution", "type"]}
                onChange={(e) => setType(e)}
                options={SELECT.CONTRIBUTE}
              />
              <CCInput label="Chim t l ... %" name={[...fieldName, "organization", "capital_contribution", "current_A_percent"]}/>
            </Form.Item>
          </Col>

          {type === "Chuyn nhng mt phn vn gp" ? (
            <>
              <Col span={24}>
                <Form.Item
                  name={[...fieldName, "organization", "capital_contribution", "will"]}
                  label="Phn vn gp bn A mun chuyn nhng"
                >
                  <InputNumber />
                </Form.Item>
              </Col>
              <Col span={24}>
                <Form.Item
                  name={[...fieldName, "organization", "capital_contribution", "transfer_price"]}
                  label="Gi chuyn nhng"
                >
                  <InputNumber />
                </Form.Item>
              </Col>
              <Col span={24}>
                <CCInput
                  name={[...fieldName, "organization", "capital_contribution", "time_end"]}
                  label="Thi im hon thnh vic chuyn nhng (Chn Ngy/ thng/ nm)"
                  type="date"
                />
              </Col>
            </>
          ) : (
            ""
          )}
        </Row>
      );
    }
    return xhtml;
  };

  return (
    <Form.Item
      label={<h4>Hp ng chuyn nhng phn gp vn</h4>}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      {/* <CCInput label="Tn doanh nghip" name={[...BASE_FORM, "company_name"]} />

      <CCInput label="M s doanh nghip/ m s thu" name={[...BASE_FORM, "mst"]} /> */}

      <Form.Item label="Bn chuyn nhng phn gp vn (bn A)">
        <CCInput
          type="select"
          label="Ch s hu"
          name={[...BASE_FORM, "A_side", "owner"]}
          onChange={(e) => setSohuuA(e)}
          options={SELECT.OWNER}
        />

        {renderFormOnwerA(sohuuA, [...BASE_FORM, "A_side"])}
      </Form.Item>

      <Form.Item label="Bn nhn chuyn nhng phn vn gp (Bn B)">
        <CCInput
          type="select"
          label="Ch s hu"
          name={[...BASE_FORM, "B_side", "owner"]}
          onChange={(e) => setSohuuB(e)}
          options={SELECT.OWNER}
        />

        {renderFormOwnerB(sohuuB, [...BASE_FORM, "B_side"])}
      </Form.Item>
    </Form.Item>
  );
});

export default HopDongChuyenNhuong;
