import { Form } from "antd";
import clsx from "clsx";
import React, { forwardRef } from "react";
import CCInput from "src/components/CCInput";
import styles from "../DaiDienPhapLuat/styles.module.scss";

const DaiDienToChuc = forwardRef((props, ref) => {
  return (
    <Form.Item
      label={<h4>Ngi i din theo y quyn ca ch s hu l t chc</h4>}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      <CCInput label="Tn doanh nghip" name={["change_info", "present_change", "company_name"]} />

      <CCInput label="M s doanh nghip/ m s thu" name={["change_info", "present_change", "mst"]} />
    </Form.Item>
  );
});

export default DaiDienToChuc;
