import React, { forwardRef, useState, useEffect } from "react";
import { Form, Row, Col, InputNumber } from "antd";
import CCInput from "src/components/CCInput";
import { FormFieldText } from "src/contants/Common";
import { number_format } from "src/helper/Common";
import clsx from "clsx";
import styles from "./styles.module.scss";
import { SELECT } from "src/contants/Common";

const ThanhVienGopVon = forwardRef((props, ref) => {
  const [present, setPresent] = useState("");

  const { current, BASE_FORM } = props;

  useEffect(() => {
    if (present === "organization") {
      ref.current.setFieldsValue({
        create_company: {
          approve: {
            origin_person: {
              company: {
                national: "Vit Nam",
              },
            },
          },
        },
      });
    }
  }, [present]);

  const renderPresentPerson = () => {
    let xhtml = null;

    if (present === "personal") {
      xhtml = (
        <div className={styles.groupInput}>
          <CCInput name={[...BASE_FORM, "origin_person", "name"]} label={FormFieldText["origin_person"]} />

          <CCInput type="date" name={[...BASE_FORM, "origin_person", "birth_day"]} label="Ngy sinh" />

          <CCInput
            type="select"
            name={[...BASE_FORM, "origin_person", "gender"]}
            label="Gii tnh"
            options={SELECT.GENDER}
          />

          <CCInput name={[...BASE_FORM, "origin_person", "per_type"]} label="Dn tc" />

          <CCInput name={[...BASE_FORM, "origin_person", "national"]} label="Quc tch" />

          <CCInput name={[...BASE_FORM, "origin_person", "reg_address"]} label="Ni ng k h khu thng tr" />

          <CCInput name={[...BASE_FORM, "origin_person", "current_address"]} label="Ch  hin ti" />

          <CCInput
            type="select"
            name={[...BASE_FORM, "origin_person", "doc_type"]}
            label="Loi giy t"
            defaultValue="Chng minh nhn dn"
            options={SELECT.DOC_TYPE}
          />

          <CCInput label={"S CMND/ CCCD/ H chiu"} name={[...BASE_FORM, "origin_person", "doc_code"]} />

          <CCInput type="date" name={[...BASE_FORM, "origin_person", "doc_time_provide"]} label="Ngy cp" />

          <CCInput name={[...BASE_FORM, "origin_person", "doc_place_provide"]} label="Ni cp" />

          <Form.Item label="a ch thng tr" className={styles.newLine}>
            <CCInput
              label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
              name={[...BASE_FORM, "origin_person", "current", "address"]}
            />

            <CCInput label="X/Phng/Th trn" name={[...BASE_FORM, "origin_person", "current", "town"]} />

            <CCInput
              label="Qun/Huyn/Th x/Thnh ph thuc tnh"
              name={[...BASE_FORM, "origin_person", "current", "district"]}
            />

            <CCInput label="Tnh/Thnh ph" name={[...BASE_FORM, "origin_person", "current", "city"]} />
          </Form.Item>

          <Form.Item label="a ch lin lc" className={styles.newLine}>
            <CCInput
              label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
              name={[...BASE_FORM, "origin_person", "contact", "address"]}
            />

            <CCInput label="X/Phng/Th trn" name={[...BASE_FORM, "origin_person", "contact", "town"]} />

            <CCInput
              label="Qun/Huyn/Th x/Thnh ph thuc tnh"
              name={[...BASE_FORM, "origin_person", "contact", "district"]}
            />

            <CCInput label="Tnh/Thnh ph" name={[...BASE_FORM, "origin_person", "contact", "city"]} />
          </Form.Item>

          <CCInput label="S in thoi" name={[...BASE_FORM, "origin_person", "contact", "phone"]} />

          <Form.Item name={[...BASE_FORM, "company_value"]} label="Gi tr gp vn">
            <InputNumber formatter={(val) => `${number_format(val)}`} style={{ width: "100%" }} />
          </Form.Item>
        </div>
      );
    } else if (present === "organization") {
      xhtml = (
        <div className={styles.groupInput}>
          <CCInput name={[...BASE_FORM, "origin_person", "name"]} label={"Tn ngi i din"} />

          <CCInput
            type="select"
            name={[...BASE_FORM, "origin_person", "title"]}
            label="Chc danh"
            options={SELECT.TITLE}
          />

          <CCInput type="date" name={[...BASE_FORM, "origin_person", "birth_day"]} label="Ngy sinh" />

          <CCInput
            type="select"
            name={[...BASE_FORM, "origin_person", "gender"]}
            label="Gii tnh"
            options={SELECT.GENDER}
          />

          <CCInput name={[...BASE_FORM, "origin_person", "per_type"]} label="Dn tc" />

          <CCInput name={[...BASE_FORM, "origin_person", "national"]} label="Quc tch" />

          <Form.Item label="a ch tr s chnh">
            <CCInput
              name={[...BASE_FORM, "origin_person", "company", "address"]}
              label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
            />

            <CCInput name={[...BASE_FORM, "origin_person", "company", "town"]} label="X/Phng/Th trn" />

            <CCInput
              name={[...BASE_FORM, "origin_person", "company", "district"]}
              label="Qun/Huyn/Th x/Thnh ph thuc tnh"
            />

            <CCInput name={[...BASE_FORM, "origin_person", "company", "city"]} label="Tnh/Thnh ph" />

            <CCInput name={[...BASE_FORM, "origin_person", "company", "national"]} label="Quc gia" />
          </Form.Item>

          <Form.Item label="a ch lin lc">
            <CCInput
              name={[...BASE_FORM, "origin_person", "contact", "address"]}
              label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
            />

            <CCInput name={[...BASE_FORM, "origin_person", "company_town"]} label="X/Phng/Th trn" />

            <CCInput
              name={[...BASE_FORM, "origin_person", "contact", "district"]}
              label="Qun/Huyn/Th x/Thnh ph thuc tnh"
            />

            <CCInput name={[...BASE_FORM, "origin_person", "contact", "city"]} label="Tnh/Thnh ph" />
          </Form.Item>

          <CCInput label={"Tn t chc"} name={[...BASE_FORM, "origin_person", "organization_name"]} />

          <CCInput
            type="select"
            name={[...BASE_FORM, "origin_person", "doc_type"]}
            label="Loi giy t"
            defaultValue="Chng minh nhn dn"
            disabled
            defaultActiveFirstOption
            options={[
              {
                name: "Chng minh nhn dn",
                value: "Chng minh nhn dn",
              },
            ]}
          />

          <CCInput label={"Chng minh nhn dn"} name={[...BASE_FORM, "origin_person", "doc_code"]} />

          <CCInput type="date" name={[...BASE_FORM, "origin_person", "doc_time_provide"]} label="Ngy cp" />

          <CCInput name={[...BASE_FORM, "origin_person", "doc_place_provide"]} label="Ni cp" />

          <CCInput label="S in thoi" name={[...BASE_FORM, "origin_person", "contact", "phone"]} />

          <Form.Item name={[...BASE_FORM, "company_value"]} label="Gi tr gp vn">
            <InputNumber formatter={(val) => `${number_format(val)}`} style={{ width: "100%" }} />
          </Form.Item>
        </div>
      );
    }
    return xhtml;
  };
  return (
    <Form.Item
      label={<h3>Thnh vin gp vn</h3>}
      className={clsx([
        styles.hide,
        {
          [styles.visible]: current === 2,
        },
      ])}
    >
      <Row gutter={[16, 12]}>
        <Col span={24}>
          <CCInput
            type="select"
            label="Ngi i din"
            name={[...BASE_FORM, "present_person"]}
            onSelect={(e) => setPresent(e)}
            // defaultValue="personal"
            defaultActiveFirstOption
            // placeholder="Chn ngi i din"
            options={[
              { value: "personal", name: "Ngi i din l c nhn" },
              { value: "organization", name: "Ngi i din l t chc" },
            ]}
          />
        </Col>
        <Col span={24}>{renderPresentPerson()}</Col>
      </Row>
    </Form.Item>
  );
});

export default ThanhVienGopVon;
