import React, { forwardRef, useEffect, useState } from 'react';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import { Button, Col, Form, Row, Space } from 'antd';
import { SELECT } from 'src/contants/Common';
import { PENDING_FORM } from 'src/contants/FormConstant';
import CCInput from 'src/components/CCInput';
import clsx from 'clsx';
import styles from '../styles.module.scss';
import CCListForm from 'src/components/CCListForm';

const BASE_FORM = ['pending', 'approve'];

const TamNgungKinhDoanh = forwardRef((props, ref) => {
  
  const [objective, setObjective] = useState('');

  const [type, setType] = useState();

  const handleChange = (e, pathname) => {
    ref.current.setFields([
      {
        name: [pathname],
        value: e.target.value.toUpperCase(),
      },
    ]);
  };

  useEffect(() => {
    if (props.data) {
      setType(props.data.type);
    }
  }, [props]);

  const renderFormByType = () => {
    let xhtml = null;

    if (type === '3') {
      let listForm = [
        {
          label: PENDING_FORM.approve.fields.list_president.president,
          placeholder: 'NGUYN VN A',
          name: 'president',
          options: {
            toUpperCase: true,
            compare: {
              end: 5,
              index: 2,
            },
            customLabel: 'Nhp tn thnh vin HQT ',
          },
        },
      ];
      // BASE_FORM, listForm, listName, addBtn, formLength;

      xhtml = (
        <>
          <CCInput
            label={PENDING_FORM.approve.fields.location}
            name={[...BASE_FORM, 'location']}
            placeholder="a ch tr s chnh"
          />

          <CCListForm
            label="Hi ng qun tr"
            BASE_FORM={BASE_FORM}
            listForm={listForm}
            formLength={5}
            defaultLength={3}
            btnText="Thm thnh vin HQT (nu c)"
            listName="list_president"
            ref={ref}
          />
        </>
      );
    } else if (type === '2') {
      let listForm = [
        {
          label: PENDING_FORM.approve.fields.contribute_members.name,
          placeholder: 'NGUYN VN A',
          name: 'name',
          options: {
            toUpperCase: true,
            compare: {
              end: 5,
              index: 2,
            },
            customLabel: 'Nhp tn thnh vin gp vn th ',
          },
        },
        {
          label: PENDING_FORM.approve.fields.contribute_members.capital,
          placeholder: '80,000,000',
          name: 'capital',
          options: {
            column: 12,
            layout: 'horizontal',
          },
        },
        {
          label: PENDING_FORM.approve.fields.contribute_members.capital_percent,
          placeholder: '80',
          name: 'capital_percent',
          options: {
            column: 12,
            layout: 'horizontal',
          },
        },
      ];
      // BASE_FORM, listForm, listName, addBtn, formLength;

      xhtml = (
        <>
          <CCInput
            label={PENDING_FORM.approve.fields.location}
            name={[...BASE_FORM, 'location']}
            placeholder="a ch tr s chnh"
          />

          <CCListForm
            label="Hi ng thnh vin"
            BASE_FORM={BASE_FORM}
            listForm={listForm}
            formLength={5}
            defaultLength={2}
            btnText="Thm thnh vin gp vn (nu c)"
            listName="contribute_members"
            ref={ref}
          />
        </>
      );
    }
    return xhtml;
  };

  return (
    <Form.Item
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      <CCInput
        name={[...BASE_FORM, 'company_name']}
        label="Nhp tn doanh nghip"
        placeholder="CNG TY TNHH DCH V T VN WARREN B"
        onChange={(e) => handleChange(e, [...BASE_FORM, 'company_name'])}
      />

      <CCInput name={[...BASE_FORM, 'mst']} label="M s doanh nghip hoc M s thu" placeholder="0316184427" />

      <CCInput
        name={[...BASE_FORM, 'org_person']}
        label="Ngi i din php lut (nhp y  h v tn)"
        placeholder="NGUYN VN A"
        onChange={(e) => handleChange(e, [...BASE_FORM, 'org_person'])}
      />

      {type && renderFormByType()}

      <CCInput
        type="select"
        name={[...BASE_FORM, 'obj']}
        label="Chn i tng thng bo tm ngng kinh doanh"
        onChange={(val, opt) => setObjective(val)}
        options={SELECT.BUSINESS_OBJECT}
        placeholder="Bm vo y"
      />

      {objective === 'Chi nhnh hoc Vn phng i din hoc a im kinh doanh' && (
        <>
          <CCInput
            name={[...BASE_FORM, 'branch_name']}
            label={
              <>
                Nhp tn&nbsp;<i> (ca Chi nhnh hoc Vn phng i din hoc a im kinh doanh)</i>
              </>
            }
            placeholder="CHI NHNH CNG TY TNHH DCH V T VN WARREN B"
            onChange={(e) => handleChange(e, [...BASE_FORM, 'branch_name'])}
          />
          <CCInput
            name={[...BASE_FORM, 'resp_office']}
            label={
              <>
                Nhp M s&nbsp;<i> (ca Chi nhnh hoc Vn phng i din hoc a im kinh doanh)</i>
              </>
            }
            placeholder="0316184427 - 001"
          />
        </>
      )}

      <Form.Item
        label={
          <>
            Thi gian ng k tm ngng kinh doanh&nbsp;
            <i> (ti a 12 thng, ngy bt u tm ngng sau t nht 3 ngy lm vic k t ngy np h s)</i>
          </>
        }
      >
        <Row gutter={[16, 12]}>
          <Col span={12}>
            <CCInput
              name={[...BASE_FORM, 'time_range', 'start']}
              label="T ngy"
              type="date"
              layout="horizontal"
              placeholder="Chn ngy"
            />
          </Col>
          <Col span={12}>
            <CCInput
              name={[...BASE_FORM, 'time_range', 'end']}
              label="n ngy"
              type="date"
              layout="horizontal"
              placeholder="Chn ngy"
            />
          </Col>
        </Row>
      </Form.Item>
    </Form.Item>
  );
});

export default TamNgungKinhDoanh;
