import { Form } from "antd";
import clsx from "clsx";
import React from "react";
import CCInput from "src/components/CCInput";
import styles from "../styles.module.scss";

const GiaiThe = (props) => {
  return (
    <Form.Item
      label="Gii th"
      className={clsx(styles.current, { 
        [styles.active]: props.current === props.index,
      })}
    >
      <CCInput name={["dissolution", "approve", "company_name"]} label="Tn doanh nghip (ghi bng ch in hoa)" />

      <CCInput name={["dissolution", "approve", "mst"]} label="M s doanh nghip/M s thu" />

      <CCInput name={["dissolution", "approve", "time_provide"]} label="Ngy cp" type="date" />

      <CCInput name={["dissolution", "approve", "place_provide"]} label="Ni cp" />
      <CCInput
        name={["dissolution", "approve", "opt_code"]}
        label="S Giy chng nhn ng k kinh doanh (ch k khai nu khng c m s doanh nghip/m s thu)"
      />

      <CCInput name={["dissolution", "approve", "company_address"]} label="a ch tr s chnh" />

      <CCInput name={["dissolution", "approve", "reason"]} label="L do gii th" />

      <CCInput name={["dissolution", "approve", "representative"]} label="ng (b) Ch s hu/i din ch s hu" />
    </Form.Item>
  );
};

export default GiaiThe;
