import { Button, Form } from "antd";
import clsx from "clsx";
import React, { forwardRef, useEffect } from "react";
import CCInput from "src/components/CCInput";
import { SELECT } from "src/contants/Common";
import styles from "./styles.module.scss";

const BASE_FORM = ["change_info", "legal_representative"];

const DaiDienPhapLuat = forwardRef((props, ref) => {
  // console.log(props);
  useEffect(() => {
    ref.current.setFieldsValue({
      change_info: {
        legal_representative: {
          national: "Vit Nam",
        },
      },
    });
    // }
  }, [ref]);

  const handleFill = () => {
    if (!ref) return;
    let val = ref.current.getFieldsValue();
    let { reg_address, town, district, city } = val.change_info.legal_representative;

    ref.current.setFieldsValue({
      change_info: {
        legal_representative: {
          contact_reg_address: reg_address,
          contact_town: town,
          contact_district: district,
          contact_city: city,
        },
      },
    });
  };

  return (
    <Form.Item
      label={<h4>ng k thay i ngi i din theo php lut</h4>}
      className={clsx(styles.current, {
        [styles.active]: props.current === props.index,
      })}
    >
      {/* <CCInput label="Tn doanh nghip" name={[...BASE_FORM, "company_name"]} />

      <CCInput label="M s doanh nghip/ m s thu" name={[...BASE_FORM, "mst"]} /> */}

      <CCInput label="Tn ngi i din php lut c" name={[...BASE_FORM, "old_name"]} />

      <CCInput type="select" name={[...BASE_FORM, "old_title"]} label="Chc danh" options={SELECT.TITLE} />

      <Form.Item label="Thng tin ngi i din theo php lut sau khi thay i">
        <CCInput label="H v tn" name={[...BASE_FORM, "new_name"]} />

        <CCInput type="select" name={[...BASE_FORM, "gender"]} label="Gii tnh" options={SELECT.GENDER} />

        <CCInput type="select" name={[...BASE_FORM, "new_title"]} label="Chc danh" options={SELECT.TITLE} />

        <CCInput name={[...BASE_FORM, "birth_day"]} label="Sinh ngy" type="date"  />

        <CCInput name={[...BASE_FORM, "per_type"]} label="Dn tc" />

        <CCInput name={[...BASE_FORM, "national"]} label="Quc tch" />

        <CCInput
          type="select"
          name={[...BASE_FORM, "doc_type"]}
          label="Loi giy t php l"
          options={SELECT.DOC_TYPE}
        />

        <CCInput name={[...BASE_FORM, "doc_code"]} label="S CMND/ CCCD/ H chiu" />

        <CCInput name={[...BASE_FORM, "doc_time_provide"]} label="Ngy cp" type="date" />

        <CCInput name={[...BASE_FORM, "doc_place_provide"]} label="Ni cp" />

        <Form.Item label="a ch thng tr">
          <CCInput name={[...BASE_FORM, "reg_address"]} label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn" />
          <CCInput name={[...BASE_FORM, "town"]} label="X/Phng/Th Trn" />
          <CCInput name={[...BASE_FORM, "district"]} label="Qun/Huyn/Th X/Thnh ph thuc tnh" />
          <CCInput name={[...BASE_FORM, "city"]} label="Tnh/Thnh ph" />
        </Form.Item>
        <Form.Item label="a ch lin lc">
          <Button onClick={handleFill}>T ng in</Button>
          <CCInput
            name={[...BASE_FORM, "contact_reg_address"]}
            label="S nh, ngch, hm, ng, ng ph/t/xm/p/thn"
          />
          <CCInput name={[...BASE_FORM, "contact_town"]} label="X/Phng/Th Trn" />
          <CCInput name={[...BASE_FORM, "contact_district"]} label="Qun/Huyn/Th X/Thnh ph thuc tnh" />
          <CCInput name={[...BASE_FORM, "contact_city"]} label="Tnh/Thnh ph" />
        </Form.Item>
      </Form.Item>
    </Form.Item>
  );
});

export default DaiDienPhapLuat;
